import React, { Component } from "react";
import styles from "./styles.module.scss";
import SectionInfo from "../SectionInfo";
import Button from "../Button";
import TelegramLoginButton from "../TelegramLoginButton";

class SectionMain extends Component {
  render() {
    const handleTelegramResponse = response => {
      console.dir(response);
      alert(
        "You shall not pass:\n" +
          "auth_date: " +
          response.auth_date +
          "\n" +
          "first_name: " +
          response.first_name +
          "\n" +
          "id: " +
          response.id +
          "\n" +
          "last_name: " +
          response.last_name +
          "\n" +
          "last_name: " +
          response.username,
      );

      const myInit = {
        method: "PUT",
        mode: "cors",
        body: { user: response },
        cache: "default",
      };

      fetch(
        "https://forge-development.herokuapp.com/auth/telegram/callback",
        myInit,
      )
        .then(r => {
          console.log("response: ");
          console.log(r);
          return r;
          // return r.json()
        })
        .then(data => {
          console.log(data);
          console.log(data.json());
        });
      setDataToStorage(response);
      console.log(localStorage);
    };

    const setDataToStorage = response => {
      const {
        id,
        auth_date,
        first_name,
        last_name,
        username,
        photo_url,
      } = response;
      localStorage.setItem("telegramID", id);
      localStorage.setItem("firstName", first_name);
      localStorage.setItem("lastName", last_name);
      localStorage.setItem("authDate", auth_date);
      localStorage.setItem("userName", username);
      localStorage.setItem("photoUrl", photo_url);
    };

    const logoutHandler = () => {
      localStorage.clear();
    };
    const text = "logout";
    const { isLogged } = this.props;

    const checked = isLogged ? (
      <Button text={text} onclick={logoutHandler} />
    ) : (
      <TelegramLoginButton
        dataOnauth={handleTelegramResponse}
        botName="RandomCofeeBot"
        requestAccess="write"
      />
    );

    return (
      <div className={`${styles.wrapper} ${styles.section__center}`}>
        <SectionInfo infoText="Use Telegram to be aware of upcoming meets and manage subscriptions:" />
        <div id={styles.telegram__login__container} className={styles.section}>
          {checked}
          {/*<Button text={text} onclick={logoutHandler} />*/}
          {/*<TelegramLoginButton*/}
            {/*dataOnauth={handleTelegramResponse}*/}
            {/*botName="RandomCofeeBot"*/}
            {/*requestAccess="write"*/}
          {/*/>*/}
        </div>
      </div>
    );
  }
}

export default SectionMain;
