import React, { Component } from "react";
// import PropTypes from "prop-types";
// import Button from "../Button";
import "./TelegramLoginButton.css";

export default class TelegramLoginButton extends Component {
  componentDidMount() {
    const {
      botName,
      buttonSize,
      cornerRadius,
      requestAccess,
      usePic,
      dataOnauth,
    } = this.props;
    // console.log(this.props);
    // console.log(dataOnauth)
    window.TelegramLoginWidget = {
      dataOnauth: user => {
        console.dir(user);
        dataOnauth(user);
      },
    };

    const script = document.createElement("script");
    script.src = "https://telegram.org/js/telegram-widget.js?4";
    script.setAttribute("data-telegram-login", botName);
    script.setAttribute("data-size", buttonSize);
    if (cornerRadius !== undefined) {
      script.setAttribute("data-radius", cornerRadius);
    }
    script.setAttribute("data-request-access", requestAccess);
    script.setAttribute("data-userpic", usePic);
    script.setAttribute("data-onauth", "TelegramLoginWidget.dataOnauth(user)");
    script.async = true;
    this.instance.appendChild(script);
  }

  render() {
    return (
      <div
        className="telegram__iframe__block"
        ref={component => {
          // console.log("this.props.className: " + this.props.className);
          // console.dir(component);
          // console.dir(this.instance);
          this.instance = component;
        }}
      />
    );
  }
}

